@import url("https://fonts.googleapis.com/css2?family=Ubuntu:ital@1&display=swap");
* {
  font-family: "Ubuntu", sans-serif;
}
body {
  background-color: rgb(58, 5, 83);
}
.App {
  background-color: #fc3778;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}
#to-do-form input {
  background-color: rgb(161, 10, 167);
  border: 0;
  color: #fff;
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
}
#to-do-form input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}
#to-do-form input:focus {
  outline: none;
}
#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(253, 142, 142);
  cursor: pointer;
  font-weight: bolder;
  color: rgb(14, 13, 13);
}
