.list {
  color: blanchedalmond;
  width: 300px;
  margin: 20px auto;
  height: 50px;
  background-color: #910303;
  border-radius: 5px;
}
.list p {
  padding: 10px;
  position: relative;
  transition: ease-in-out;
}
.list p span {
  position: absolute;
  right: 10px;
}
.faicons {
  margin: 0 5px;
  font-size: 15px;
  cursor: pointer;
}
.list p input {
  background-color: transparent;
  border: 0;
  color: rgb(252, 248, 248);
  font-size: 18px;
}
.list p input:focus {
  outline: none;
}
